.contactForm {
  width: 400px;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.contactForm__input {
  display: block;
  margin-bottom: 20px;
  border: none;
  // outline: 2px solid black;
  border-radius: 2px;

  &:focus {
    outline: 2px solid skyblue;
  }
}
.contactForm__btn {
  cursor: pointer;
  display: block;
  background-color: transparent;
  width: 100px;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 5px;
}
.contactForm__label {
  margin-bottom: 5px;
}
